import React, { useState } from 'react';
import { Table } from 'reactstrap';
import { Button } from "reactstrap";
import { firebaseApp } from "./Firebase";
import "../css/TableOfLecture.css";


const getShow = (number) => {
    var isShow = null;
    firebaseApp
    .database()
    .ref(
      "Lecture/Inform/Lecture" +
      number +
        "/isShow"
    )
    .on("value", function (snapshot) {
      if (snapshot.exists) {
        isShow = snapshot.val();
      }
    });
    if(isShow === true) return "Hin"
    else if (isShow === false) return "n"
    else return "Loading"
}

const TableOfLecture = (props) => {
    const [L1, setL1] = useState(getShow(1));
    const [L2, setL2] = useState(getShow(2));
    const [L3, setL3] = useState(getShow(3));
    const [L4, setL4] = useState(getShow(4));
    const [L5, setL5] = useState(getShow(5));
    const [L6, setL6] = useState(getShow(6));
    const [L7, setL7] = useState(getShow(7));
    const [L8, setL8] = useState(getShow(8));
    const [L9, setL9] = useState(getShow(9));
    const [L10, setL10] = useState(getShow(10));

    const setShow = (number, isShow) => {
        if(number === 1 && isShow=== true) setL1("Hin")
        if(number === 1 && isShow=== false) setL1("n")
        if (number === 1) {
            if (isShow === true) setL1("Hin")
            else setL1("n")
        }
        else if (number === 2) {
            if (isShow === true) setL2("Hin")
            else setL2("n")
        }
        else if (number === 3) {
            if (isShow === true) setL3("Hin")
            else setL3("n")
        }
        else if (number === 4) {
            if (isShow === true) setL4("Hin")
            else setL4("n")
        }
        else if (number === 5) {
            if (isShow === true) setL5("Hin")
            else setL5("n")
        }
        else if (number === 6) {
            if (isShow === true) setL6("Hin")
            else setL6("n")
        }
        else if (number === 7) {
            if (isShow === true) setL7("Hin")
            else setL7("n")
        }
        else if (number === 8) {
            if (isShow === true) setL8("Hin")
            else setL8("n")
        }
        else if (number === 9) {
            if (isShow === true) setL9("Hin")
            else setL9("n")
        }
        else if (number === 10) {
            if (isShow === true) setL10("Hin")
            else setL10("n")
        }
        firebaseApp
          .database()
          .ref(
            "Lecture/Inform/Lecture" + number
          )
          .set({
            isShow: isShow,
          });
    }

  return (
    <div className="tableOfLecture">  
        <Table hover>
        <thead>
            <tr>
            <th>Mn hc</th>
            <th>Chng</th>
            <th>Tn chng</th>
            <th>Trng thi</th>
            <th>n/Hin</th>
            </tr>
        </thead>
        <tbody>
            <tr>
            <td>K thut lp trnh C</td>
            <td>1</td>
            <td>Gii thiu</td>
            <td>{L1}</td>
            <td>
                <Button id="hide" color="info" onClick={()=>setShow(1, true)}>Hin</Button>
                <Button id="show" color="danger" onClick={()=>setShow(1, false)}>n</Button>
            </td>
            </tr>
        </tbody>
        <tbody>
            <tr>
            <td>K thut lp trnh C</td>
            <td>2</td>
            <td>Kiu d liu c s v cc php ton</td>
            <td>{L2}</td>
            <td>
                <Button id="hide" color="info" onClick={()=>setShow(2, true)}>Hin</Button>
                <Button id="show" color="danger" onClick={()=>setShow(2, false)}>n</Button>
            </td>
            </tr>
        </tbody>
        <tbody>
            <tr>
            <td>K thut lp trnh C</td>
            <td>3</td>
            <td>Cu trc iu khin</td>
            <td>{L3}</td>
            <td>
                <Button id="hide" color="info" onClick={()=>setShow(3, true)}>Hin</Button>
                <Button id="show" color="danger" onClick={()=>setShow(3, false)}>n</Button>
            </td>
            </tr>
        </tbody>
        <tbody>
            <tr>
            <td>K thut lp trnh C</td>
            <td>4</td>
            <td>Con tr v hm</td>
            <td>{L4}</td>
            <td>
                <Button id="hide" color="info" onClick={()=>setShow(4, true)}>Hin</Button>
                <Button id="show" color="danger" onClick={()=>setShow(4, false)}>n</Button>
            </td>
            </tr>
        </tbody>
        <tbody>
            <tr>
            <td>K thut lp trnh C</td>
            <td>5</td>
            <td>Mng d liu</td>
            <td>{L5}</td>
            <td>
                <Button id="hide" color="info" onClick={()=>setShow(5, true)}>Hin</Button>
                <Button id="show" color="danger" onClick={()=>setShow(5, false)}>n</Button>
            </td>
            </tr>
        </tbody>
        <tbody>
            <tr>
            <td>K thut lp trnh C</td>
            <td>6</td>
            <td>Chui</td>
            <td>{L6}</td>
            <td>
                <Button id="hide" color="info" onClick={()=>setShow(6, true)}>Hin</Button>
                <Button id="show" color="danger" onClick={()=>setShow(6, false)}>n</Button>
            </td>
            </tr>
        </tbody>
        <tbody>
            <tr>
            <td>K thut lp trnh C</td>
            <td>7</td>
            <td>Kiu d liu cu trc</td>
            <td>{L7}</td>
            <td>
                <Button id="hide" color="info" onClick={()=>setShow(7, true)}>Hin</Button>
                <Button id="show" color="danger" onClick={()=>setShow(7, false)}>n</Button>
            </td>
            </tr>
        </tbody>
        <tbody>
            <tr>
            <td>K thut lp trnh C</td>
            <td>8</td>
            <td>Kiu tp tin</td>
            <td>{L8}</td>
            <td>
                <Button id="hide" color="info" onClick={()=>setShow(8, true)}>Hin</Button>
                <Button id="show" color="danger" onClick={()=>setShow(8, false)}>n</Button>
            </td>
            </tr>
        </tbody>
        <tbody>
            <tr>
            <td>K thut lp trnh C</td>
            <td>9</td>
            <td>K thut duyt mng s dng con tr</td>
            <td>{L9}</td>
            <td>
                <Button id="hide" color="info" onClick={()=>setShow(9, true)}>Hin</Button>
                <Button id="show" color="danger" onClick={()=>setShow(9, false)}>n</Button>
            </td>
            </tr>
        </tbody>
        <tbody>
            <tr>
            <td>K thut lp trnh C</td>
            <td>10</td>
            <td>B nh ng v ng dng trong DSLK</td>
            <td>{L10}</td>
            <td>
                <Button id="hide" color="info" onClick={()=>setShow(10, true)}>Hin</Button>
                <Button id="show" color="danger" onClick={()=>setShow(10, false)}>n</Button>
            </td>
            </tr>
        </tbody>
        </Table>
    </div>  
  );
}

export default TableOfLecture;